// src/pages/api/send-status-email.js
import { sendStatusEmail } from "../../../utils/emailUtils";
import fs from "fs";
import path from "path";

export default async function handler(req, res) {
    if (req.method !== "POST") {
        return res.status(405).json({ error: "Method not allowed" });
    }

    const { fullName, email, opening, status } = req.body;

    try {
        const templateFile = `${status.toLowerCase()}Email.html`;
        const templatePath = path.join(process.cwd(), "src/templates", templateFile);
        const template = fs.readFileSync(templatePath, "utf8");

        await sendStatusEmail({ fullName, email, opening, status, template });
        res.status(200).json({ message: "Email sent successfully" });
    } catch (error) {
        console.error("Error sending status email:", error);
        res.status(500).json({ error: "Failed to send email" });
    }
}